<template>
  <div class="jobFairBox">
    <div class="container">
      <label class="search-box">
          <input type="text" placeholder="快速搜索岗位、公司">
          <button>搜索</button>
      </label>
      <div class="quicklySearch">
        <ul>
          <li class="title">专业：</li>
          <li @click="changeType('search',index)" v-for="(item,index) in searchList" :key="index" :class="searchActive===index?'active':''">{{item.name}}</li>
        </ul>
        <ul>
          <li class="title" >融资阶段：</li>
          <li @click="changeType('step',index)" v-for="(item,index) in stepList" :key="index" :class="stepActive===index?'active':''">{{item.name}}</li>
        </ul>
        <ul>
          <li class="title">公司规模：</li>
          <li @click="changeType('people',item.value)" v-for="(item,index) in peopleList" :key="index" :class="peopleActive===item.value?'active':''">{{item.name}}</li>
        </ul>
      </div>
      <hr>
    </div>
    <div class="companyList">
      <div class="container">
        <bread :data="[{name:'首页',url:'/'},{name:'职位招聘',url:''},{name:'专业不限'.name,url:''}]"></bread>
        <div class="companyListBox">
          <ul>
            <li v-for="(item,index) in companyList" :key="index">
                <div class="listBox">
                    <router-link :to="{path:'companyDetail',query:{id:item.id}}">
                      <img :src="item.comPhotoUrl" alt="">
                    </router-link>
                    <router-link :to="{path:'companyDetail',query:{id:item.id}}">
                      <p class="name">{{item.comName}}</p>
                    </router-link>
                    <p class="step">{{item.industry}}</p>
                    <p class="needs">热招<span class="job">{{item.needs}}</span> <span>{{item.money}}</span></p>
                </div>
            </li>
          </ul>
        </div>
        <div class="page">
          <el-pagination
            :current-page="currentPage"
            :page-sizes="[1, 50, 100, 200]"
            :page-size="10"
            :current-change="currentChange"
            layout="prev, pager, next, sizes, jumper"
            :total="total">
          </el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import companyInfoVue from './compoments/companyInfo.vue'
import jobList from './compoments/jobList'
import otherInfo from './compoments/otherInfo'
import bread from './compoments/bread'
import api from '../api'
export default {
  name:'JobFair',
  components:{
    jobList,
    otherInfo,
    bread
  },
  data(){
    return{
      currentPage:1,
      total:0,
      searchList:[{name:'专业不限'},{name:'环境工程'},{name:'环境污染与治理'},{name:'环境卫生工程'},{name:'环境化工'},{name:'环境监测'},{name:'环境科学'},{name:'生态学'},{name:'环境保护与监测'},{name:'环境规划与管理'},{name:'环境工程管理'},{name:'规划与环境保护'},{name:'环境监察'},{name:'环境治理工程'}],
      stepList:[{name:'不限阶段'},{name:'未融资'},{name:'天使轮'},{name:'A轮'},{name:'B轮'},{name:'C轮'},{name:'D轮及以上'},{name:'已上市'},{name:'不需要融资'}],
      peopleList:[{name:'不限',value:''},{name:'20人以下',value:'0'},{name:'20-150人',value:'1'},{name:'150-500人',value:'2'},{name:'500-999人',value:'3'},{name:'1000以上',value:'4'}],
      searchActive:'',
      stepActive:'',
      peopleActive:'',
      companyList:[]
    }
  },
  methods: {
    init(){
      this.getList()
    },
    changeType(item,value){
      this[item+'Active'] = value
      this.getList()
    },
    getList(){
      api.post('http://47.112.170.125:8080/env/company/getList',
      {positionName:'',postName:'',recruitType:0,comNumPeople:this.peopleActive,workAdr:'',workCity:''}
      ).then(res=>{
        this.companyList = res.rows;
        this.total = res.total; 
      })
    },
    currentChange(cur){
      this.currentPage = cur

    }
  },
  mounted(){
    this.init()
  }
}
</script>
<style lang="less">
.jobFairBox{
  padding-top: 30px;
  overflow: hidden;
  .search-box{
    input{
      height: 40px;
      line-height: 40px;
      width: 700px;
    }
    button{
      height: 40px;
    }
  }
  hr{
    color:#10bbb8;
    background-color: #10bbb8;
  }
  .quicklySearch{
    margin: 10px 0 20px 0;
    ul{
      overflow: hidden;
      li{
        &.title{
          cursor:normal;
          color:#FC8863;
          width: 70px;
          text-align: left;
          margin-right: 0;
        }
        float: left;
        margin: 10px 20px 0px 0;
        cursor: pointer;
        &.active{
          color:#FC8863
        }
      }
    }
  }
  .sortSearch{
    margin: 20px 0;
    ul{
      overflow: hidden;
      li{
        float: left;
        margin-right: 40px;
        cursor: pointer;
      }
    }
  }
  .jobContainer{
    background-color: #f6f6f8;
    padding: 20px;
    text-align: left;
  }
  
    .loginModule{
      float:right;
      display: inline-block;
      width: 197px;
      border-radius: 5px;
      padding: 0 40px;
      background-color: #10bbb8;
      height: 297px;
      overflow: hidden;
      .title{
        font-size: 18px;
        text-align: center;
        color:#fff;
        padding: 20px 0;
      }
      .formItem{
        height: 36px;
        line-height: 36px;
        margin-top: 20px;
        padding: 0 10px;
        background-color: #fff;
        input{
          height: 26px;
          line-height: 26px;
          display: inline-block;
          width: 110px;
          outline: 0 none;
          border:0 none;
        }
        .psd{
          width: 140px;
        }
    }
    .toLogin{
      button{
        height: 36px;
        line-height: 36px;
        color: #10bbb8;
        outline: 0 none;
        border:0 none;
        font-size:18px;
        text-align: center;
        width: 100%;
        background-color: #fff;
        margin: 20px 0;
        &:hover{
          cursor: pointer;
        }
      }
    }
    .agree{
      input{
        vertical-align: -2px;
      }
      color: #fff;
      font-size: 12px;
    }
    }
    .companyList{
      background-color: #f6f6f8;
      .bread{
        text-align: left;
        padding: 10px 0;
      }
      .companyListBox{
        overflow: hidden;
        li{
          float: left;
          overflow: hidden;
          width: 265px;
          height: 110px;
          border:1px solid #10bbb8;
          margin-right: 30px;
          margin-bottom: 20px;
          background-color: #fff;
          img{
            width: 80px;
            height: 80px;
            float: left;
            vertical-align: middle;
            margin:15px;
            &:hover{
              cursor: pointer;
            }
          }
          .name{
            color:#333;
            font-size: 14px;
            margin: 0px 10px 0 110px;
            padding: 15px 0 5px 0;
            text-align: right;
            &:hover{
              cursor: pointer;
            }
            font-weight: bold;
          }
          .step{
            margin: 0px 10px 0 110px;
            text-align: right;
            padding: 5px 0 10px 0;
            border-bottom: 1px dashed #10bbb8;
            font-size: 12px;
            color:#666;
          }
          .needs{
            margin: 0px 10px 0 110px;
            text-align: right;
            padding: 10px 0 5px 0;
            font-size: 12px;
            color:#666;
            .job{
              color: #FC8863;
            }
          }
        }
      }
    }
    .page{
      text-align: right;
      margin: 20px 0;
    } 
    
  .el-pagination .btn-prev,.el-pagination .btn-next{
    padding: 0 6px;
    border-radius: 2px;
    border:1px solid #DCDFE6; 
  }
  .el-pagination li{
    color:#10bbb8;
    font-weight: normal;
    border:1px solid #DCDFE6;
    margin: 0 5px;
    border-radius: 4px;
  }
  .el-pager li.active+li{
    border:1px solid #DCDFE6;
  }
  .el-pager li.active{
    background-color:#10bbb8;
    color:#fff;
  }
  .el-pagination__editor.el-input .el-input__inner{
    color: #10bbb8;
  }
}
</style>


